<script lang="ts">
	import Swal from "sweetalert2";
	import {writable} from "svelte/store";
	import Tooltip from "./Tooltip.svelte";

	function clamp(n: number, min: number, max: number): number {
		if (n > max) {
			return max;
		} else if (n < min) {
			return min;
		} else {
			return n;
		}
	}

	function calculateTimeFraction() {
		const rawTimeFraction = time / maxtime;
		return rawTimeFraction;
	}

	function setCircleDasharray() {
		const circleDasharray = `${(calculateTimeFraction() * 283).toFixed(
			0
		)} 283`;
		document
			.getElementById("base-timer-path-remaining")
			.setAttribute("stroke-dasharray", circleDasharray);
	}

	function clearCountdown() {
		clearInterval(countdown);
		countdown = null;
	}

	function countdownStep() {
		if (time - 1 <= 0) {
			nextCountdown();
			clearCountdown();
		}
		if (time < 1) {
			return;
		}
		time--;
		setCircleDasharray();
	}

	function timerClick(): void {
		if (time <= 0) {
			resetCountdown();
		}
		if (!countdown) {
			countdownStep();
			if (time - 1 > 0) {
				countdown = setInterval(function () {
					countdownStep();
				}, 1000);
			}
		} else {
			clearCountdown();
		}
	}

	function resetCountdown() {
		time = maxtime;
		setCircleDasharray();
		clearCountdown();
	}

	function nextCountdown() {
		countdownIndex++;
		audio.play();
		if (countdownIndex > 7) {
			countdownIndex = 0;
		}
		if (countdownIndex % 2 == 0) {
			maxtime = TIMES[0];

			Swal.fire({
				title: "Break time is over",
				text: "Time to get back to work",
				icon: "warning",
				heightAuto: false,
			});
		} else if (countdownIndex % 2 == 1) {
			sessions++;
			localStorage.setItem("sessions", String(sessions));
			maxtime = TIMES[1];

			Swal.fire({
				title: "Good job",
				text: "You can take a break now",
				icon: "success",
				heightAuto: false,
			});
		}
		if (countdownIndex == MAXSESSIONS - 1) {
			maxtime = TIMES[2];
		}
		stats++;
	}

	function skipCountdown() {
		if (time != 0) {
			nextCountdown();
		}
		resetCountdown();
	}

	function haveABreak() {
		if (countdownIndex % 2 != 1) {
			nextCountdown();
			resetCountdown();
		}
	}

	const TIMES: number[] = [25 * 60, 5 * 60, 15 * 60];
	const MAXSESSIONS: number = 8;
	let stats: number = 0;
	let countdownIndex: number = 0;
	let maxtime: number = TIMES[0];
	let time: number = maxtime;
	let countdown: ReturnType<typeof setInterval>;
	let sessions: number = 0;
	if (!localStorage.getItem("sessions")) {
		localStorage.setItem("sessions", "0");
	} else {
		sessions = Number(localStorage.getItem("sessions"));
	}
	const audio = new Audio("blip.wav");
</script>

<main>
	<div class="circle" on:click={timerClick}>
		<div class="base-timer">
			<svg
				class="base-timer__svg"
				viewBox="0 0 100 100"
				xmlns="http://www.w3.org/2000/svg"
			>
				<g class="base-timer__circle">
					<circle
						class="base-timer__path-elapsed"
						cx="50"
						cy="50"
						r="45"
					/>
					<path
						id="base-timer-path-remaining"
						stroke-dasharray="283"
						class="base-timer__path-remaining {countdown
							? 'green'
							: time
							? 'blue'
							: 'red'}"
						d="
						M 50, 50
						m -45, 0
						a 45,45 0 1,0 90,0
						a 45,45 0 1,0 -90,0
					  "
					/>
				</g>
			</svg>
			<span id="base-timer-label" class="base-timer__label">
				{new Date(time * 1000).toISOString().substr(14, 5)}
			</span>
		</div>
	</div>
	<div class="action_buttons">
		<div class="restart button" on:click={resetCountdown}>
			<Tooltip tooltip="Restart">
				<i class="gg-sync" /></Tooltip
			>
		</div>
		<div class="skip button" on:click={skipCountdown}>
			<Tooltip tooltip="Skip">
				<i class="gg-arrow-right-o" /></Tooltip
			>
		</div>
		<div class="skip button" on:click={haveABreak}>
			<Tooltip tooltip="Have a break">
				<i class="gg-coffee" /></Tooltip
			>
		</div>
	</div>
	<h4>You have completed {sessions} session(s)</h4>
</main>
